@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.Navbar {
  box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
}

.Logo {
  padding: 30px;
  width: 180px;
}

.Hero {
  display: flex;
  flex-direction: column;
}

.grid {
  align-self: center;
  padding-top: 50px;



}

.para-1 {
  font-weight: 300;
  font-size: 1rem;
  padding-left: 20px;
  max-width: 350px;
}

.heading-1 {
  padding: 20px 20px;
  font-weight: 700;
  font-size: 2.25 rems;
}

/* CARD */
.card-main {
  display: flex;
  flex-wrap: nowrap;
}

.Card {
  width: 185px;
  height: 305px;
  margin-left: 20px;
  margin-top: 50px;
  position: relative;
}

.girl-img {
  max-width: 180px;
  border-radius: 10px;
  height: 235px;
}

.Star {
  width: 14px;
  margin-right: 2px;
}

.info {
  display: flex;
  font-size: 0.75rem;
  font-weight: 400;
  align-items: center;
}

.fonty {
  font-weight: 350;
  color: #918E9B;
}

.para-2 {
  font-weight: 400;
  font-size: 0.75rem;
  padding: 2px 0 2px 0;
}

.bold {
  font-weight: 700;
  font-size: 0.75rem;
}

.overlay {
  font-size: 0.65rem;
  position: absolute;
  background-color: white;
  width: 60px;
  height: 20px;
  top: 9px;
  left: 9px;
  font-weight: 600;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
}